import Vue from 'vue'
import store from './store'


Vue.prototype.doGroupAction = function(group, action) {
  let v = {to: 'one'}; v[group] = {id: action};
  Vue.prototype.$socket.send('groups', 'action', v);
};

Vue.prototype.doMinerAction = function(miner, action) {
  let v = {to: 'one'}; v[miner] = {id: action};
  Vue.prototype.$socket.send('machines', 'action', v);
};

Vue.prototype.convertTemperature = function(kelvin, display=false) {
  let ret = [kelvin, 'K'];

  if(kelvin === undefined || kelvin == 0) {
    return !display ? [0, null] : 'N/A';
  }else if(localStorage.temperature != 'K') {
    if(localStorage.temperature == 'C') ret = [kelvin - 273.15, 'C'];
    if(localStorage.temperature == 'F') ret = [(kelvin - 273.15) * 1.8 + 32, 'F'];
  }

  return !display ? ret : ret[0].toFixed(0) + ' °' + ret[1];
};

Vue.prototype.convertRate = function(hashes, display=false, minimum='h') {
  let ret = [hashes, ''];

  if(hashes === undefined) {
    return !display ? [0, null] : 'N/A';
  }else{
    if(minimum == 'TH' || hashes / 10000000000000 > 1) ret = [hashes / 1000000000000, 'TH'];
    else if(minimum == 'GH' || hashes / 10000000000 > 1) ret = [hashes / 1000000000, 'GH'];
    else if(minimum == 'MH' || hashes / 10000000 > 1) ret = [hashes / 1000000, 'MH'];
    else if(minimum == 'kH' || hashes / 10000 > 1) ret = [hashes / 1000, 'kH'];
  }

  return !display ? ret : ret[0].toFixed(ret[1] !== '' ? 2 : 2) + ' ' + ret[1];
};


Vue.prototype.isSynced = function(group_id, miner) {
  if(miner.dummy) return true;

  if(group_id != miner.group.id) return false;

  let group = store.state.groups[group_id];
  if(!group) return false;

  if(group.wallet) {
    if(!miner.wallet) return false;
    if(!store.state.wallets.hasOwnProperty(group.wallet)) return false;
    if(store.state.wallets[group.wallet].address != miner.wallet.address) return false;
  }else
    if(miner.wallet) return false;

  if(group.pool) {
    if(!miner.pool.endpoint) return false;
    if(!store.state.pools.hasOwnProperty(group.pool.id)) return false;

    let pool = store.state.pools[group.pool.id];
    if(!pool.endpoints.hasOwnProperty(group.pool.endpoint)) return false;

    let endpoint = pool.endpoints[group.pool.endpoint];
    if(endpoint.url != miner.pool.endpoint.url) return false;
  }

  if(group.program) {
    if(group.program.execute.args != miner.program.execute.args) return false;

    if(!store.state.software.hasOwnProperty(group.program.id)) return false;

    let program = store.state.software[group.program.id];
    if(program.name != miner.program.name) return false;
  }

  return true;
};


Vue.prototype.getOnlineMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

    let stats = store.state.miner_stats[id];
    if(!stats) continue;

    t += stats && stats.status.type != 'offline' ? 1 : 0;
  }
  return t;
};

Vue.prototype.getUnsyncedMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

    t += this.isSynced(store.state.machines[id].group.id, store.state.machines[id]) ? 0 : 1;
  }
  return t;
};

Vue.prototype.getOfflineMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

    let stats = store.state.miner_stats[id];

    t += !stats || stats.status.type != 'offline' ? 0 : 1;
  }
  return t;
};

Vue.prototype.getDisconnectedMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

    let stats = store.state.miner_stats[id];

    t += stats && stats.connected ? 0 : 1;
  }
  return t;
};

Vue.prototype.getTotalMiners = function(opt={}) {
  let t = 0;
  for(let id in store.state.machines) {
    let miner = store.state.machines[id];
    if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;
    t += 1;
  }
  return t;
};


Vue.prototype.getMaxTemperature = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        if(stats.hardware.gpus[j].temp > t)
          t = stats.hardware.gpus[j].temp;
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      if(stats.hardware.gpus[i].temp > t)
        t = stats.hardware.gpus[i].temp;
  }
  return t;
};

Vue.prototype.getTotalWatts = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        t += stats.hardware.gpus[j].watts;
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      t += stats.hardware.gpus[i].watts;
  }
  return t;
};

Vue.prototype.getTotalRate = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        t += stats.hardware.gpus[j].rate;
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      t += stats.hardware.gpus[i].rate;
  }
  return t;
};

Vue.prototype.getOnlineGPUs = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        t += (stats.hardware.gpus[j].status.type == 'online' ? 1 : 0);
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      t += (stats.hardware.gpus[i].status.type == 'online' ? 1 : 0);
  }
  return t;
};

Vue.prototype.getOfflineGPUs = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        t += (stats.hardware.gpus[j].status.type == 'offline' ? 0 : 1);
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      t += (stats.hardware.gpus[i].status.type == 'offline' ? 0 : 1);
  }
  return t;
};

Vue.prototype.getTotalGPUs = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group && (!miner.group || miner.group.id != opt.group)) continue;

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      t += stats.hardware.gpus.length;
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    t += stats.hardware.gpus.length;
  }
  return t;
};



