<template>
  <v-layout row wrap>
    <v-flex xs12 md7 class="pa-2">
      <v-card>
        <v-card-title>
          <v-layout row wrap>
            <v-flex xs2 sm1>
              <miner-status :miner_id="miner_id" large />
            </v-flex>
            <v-flex xs10 sm9>
              <v-text-field
                style="padding:0"
                flat
                hide-details
                single-line
                v-model="miner.name"
                @blur="pushUpdate()">
              </v-text-field>
            </v-flex>
            <v-flex xs6 sm1 class="text-xs-center">
              <v-tooltip top>
                <v-btn small flat icon slot="activator" @click="dialog.refresh.id = id; dialog.refresh.shown = true">
                  <v-icon class="indigo--text">sync</v-icon>
                </v-btn>
                <span>Refresh</span>
              </v-tooltip>
            </v-flex>
            <v-flex xs6 sm1 class="text-xs-center">
              <v-menu left>
                <v-btn small flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
                <v-list>
                  <v-list-tile>
                    <v-list-tile-title class="grey--text text-xs-center">{{ miner.hardware.mac }}</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.reboot.id = id; dialog.reboot.shown = true">
                    <v-list-tile-title><v-icon class="small indigo--text">fa-refresh</v-icon> Refresh</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.reboot.id = id; dialog.reboot.shown = true">
                    <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.wake.id = id; dialog.wake.shown = true">
                    <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.shutdown.id = id; dialog.shutdown.shown = true">
                    <v-list-tile-title><v-icon class="small red--text">fa-power-off</v-icon> Shutdown</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </v-flex>
          </v-layout>
        </v-card-title>
        <v-card-text>
          <v-text-field
            style="margin-top:-30px"
            textarea
            hide-details
            label="Notes"
            v-model="miner.notes"
            @blur="pushUpdate()">
          </v-text-field>
        </v-card-text>
      </v-card>
      <v-card class="mt-3">
        <v-card-title>
          <h4 class="headline mb-0">Specs and Storage</h4>
        </v-card-title>
        <v-layout row wrap>
          <v-flex md6 class="pa-3" v-for="(cpu, i) in miner.hardware.cpus" :key="'cpu-' + i">
            <v-card raised>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex xs3>
                    <v-icon x-large>fa-cogs</v-icon>
                  </v-flex>
                  <v-flex xs9>
                    Processor<br />
                    <small>{{ cpu.product }}</small>
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex md6 class="pa-3" v-for="(memory, i) in miner.hardware.memory" :key="'mem-' + i">
            <v-card raised>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex xs3>
                    <v-icon x-large>fa-tasks</v-icon>
                  </v-flex>
                  <v-flex xs9>
                    Memory<br />
                    <small>{{ (memory.size / 1024 / 1024 / 1024).toFixed(2) }} GB</small>
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
        <table class="table">
          <thead>
            <tr>
              <th class="text-xs-left">Type</th>
              <th class="text-xs-left">>Mount Point</th>
              <th style="width:120px">Usage</th>
              <th style="width:120px">Size</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(storage, i) in miner.hardware.storage" :key="'mount-' + i">
              <td>{{ storage.fstype }}</td>
              <td>{{ storage.mount }}</td>
              <td>{{ (storage.space.used / 1024 / 1024 / 1024).toFixed(2) }} GB</td>
              <td>{{ (storage.space.total / 1024 / 1024 / 1024).toFixed(2) }} GB</td>
            </tr>
          </tbody>
        </table>
      </v-card>
    </v-flex>
    <v-flex xs12 md5 class="pa-2">
      <v-card class="mb-3">
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12 class="text-xs-center">
              <v-icon class="small" color="orange">fa-hashtag</v-icon> {{ convertRate(getTotalRate({miner: miner_id}), true) }}/s
            </v-flex>
          </v-layout>
          <v-divider class="ma-2" />
          <v-layout row wrap>
            <v-flex xs6 class="text-xs-center">
              <v-icon class="small" color="orange">fa-bolt</v-icon> {{ getTotalWatts({miner: miner_id}).toFixed(2) }} W<br />
            </v-flex>
            <v-flex xs6 class="text-xs-center">
              <v-icon class="small" color="orange">fa-bolt</v-icon> {{ convertRate(getTotalRate({miner: miner_id}) / getTotalWatts({miner: miner_id}), true) }} per Watt
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <v-card>
        <v-card-text>
          <v-subheader>
            GPUs
          </v-subheader>
          <template v-for="(gpu, i) in miner.hardware.gpus">
            <v-container grid-list-xs class="py-3" :key="'gpu-' + i">
              <v-layout row wrap>
                <v-flex xs1>
                  <div v-if="miner_stats && miner_stats.hardware.gpus[i]">
                    <v-tooltip top v-if="miner_stats.hardware.gpus[i].status.type == 'online'">
                      <v-icon class="success--text" slot="activator">label</v-icon>
                      <span>Online</span>
                    </v-tooltip>
                    <v-tooltip top v-else-if="miner_stats.hardware.gpus[i].status.type == 'unstable'">
                      <v-icon class="warning--text" slot="activator">label</v-icon>
                      <span>Unstable</span>
                    </v-tooltip>
                    <v-tooltip top v-else>
                      <v-icon class="error--text" slot="activator">label</v-icon>
                      <span>Offline</span>
                    </v-tooltip>
                  </div>
                  <v-icon class="grey--text" v-else>label</v-icon>
                </v-flex>
                <v-flex xs11>
                  <div class="gpu-name"><span>{{ gpu.product }}</span></div>
                </v-flex>
              </v-layout>
              <v-layout row wrap class="text-xs-center">
                <v-flex xs6 md3>
                  <v-icon class="small" color="blue-grey">fa-leaf</v-icon>
                  <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ miner_stats.hardware.gpus[i].fan }}%</div>
                  <div v-else> N/A</div>
                </v-flex>
                <v-flex xs6 md3>
                  <v-icon class="small" color="orange">fa-thermometer-empty</v-icon>
                  <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ convertTemperature(miner_stats.hardware.gpus[i].temp, true) }}</div>
                  <div v-else> N/A</div>
                </v-flex>
                <v-flex xs6 md3>
                  <v-icon class="small" color="orange">fa-bolt</v-icon>
                  <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ miner_stats.hardware.gpus[i].watts }} W</div>
                  <div v-else> N/A</div>
                </v-flex>
                <v-flex xs6 md3>
                  <div v-if="miner_stats && miner_stats.hardware.gpus[i]">
                    <v-tooltip top>
                      <div slot="activator">
                        <v-icon class="small" color="orange">fa-hashtag</v-icon>
                        <div>{{ convertRate(miner_stats.hardware.gpus[i].rate, true) }}/s</div>
                      </div>
                      <span>{{ convertRate(miner_stats.hardware.gpus[i].rate / miner_stats.hardware.gpus[i].watts, true) }} per Watt</span>
                    </v-tooltip>
                  </div>
                  <div v-else>
                    <v-icon class="small" color="orange">fa-hashtag</v-icon>
                    <div>N/A</div>
                  </div>
                </v-flex>
              </v-layout>
            </v-container>
          </template>
        </v-card-text>
      </v-card>
    </v-flex>

    <v-dialog v-model="dialog.refresh.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to refresh this miner?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.refresh.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.refresh.shown = false; doAction('refresh')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.wake.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to wake up this miner?<br />
          <small><b>Note:</b> This only works on machines with Wake-on-LAN enabled.</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.wake.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.wake.shown = false; doAction('wake')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.reboot.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to reboot this miner?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.reboot.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.reboot.shown = false; doAction('reboot')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.shutdown.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to shutdown this miner?<br />
          <small><b>Note:</b> You will have to turn them on manually if Wake-on-LAN is not enabled!</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.shutdown.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.shutdown.shown = false; doAction('shutdown')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<style>
  .gpu-name {
    border-bottom: 1px solid rgba(0,0,0,0.12);
    line-height: 0.1em;
    margin: 12px 0 20px;
  }

  .gpu-name span {
    background: #FFF; 
    padding: 0 10px 0 0;
  }
</style>


<script>
  export default {
    data() {
      return {
        dialog: {
          refresh: {
            shown: false
          },
          wake: {
            shown: false
          },
          reboot: {
            shown: false
          },
          shutdown: {
            shown: false
          }
        }
      }
    },
    computed: {
      miner_id() {
        return this.$route.params.id;
      },

      messages() {
        return this.$store.state.messages;
      },
      miner() {
        return this.$store.state.machines[this.miner_id];
      },
      miner_stats() {
        return this.$store.state.miner_stats[this.miner_id];
      }
    },
    methods: {
      doAction(action) {
        let v = {to: 'one'}; v[this.miner_id] = {id: action};
        this.$socket.send('machines', 'action', v);
        this.$notify((action == 'refresh' ? 'info' : (action == 'reboot' ? 'warning' : 'error')), 'Instructing miner to ' + action + '...');
      },
      pushUpdate() {
        let vm = this;
        this.$nextTick(function() {
          let v = {}; v[this.miner_id] = vm.$store.state.machines[this.miner_id];
          this.$socket.send('machines', 'update', v);
        });
      }
    }
  }
</script>



